import { Injectable, PipeTransform } from '@angular/core';
import { DecimalPipe } from '@angular/common';

import { BehaviorSubject, Observable, of, Subject } from 'rxjs';
import { debounceTime, delay, switchMap, tap } from 'rxjs/operators';

import { SearchResult } from './contract.model';

import { SortDirection } from './contract-sortable.directive';
import { ApiService } from 'src/app/core/services/api.service';

interface State {
   page: number;
   pageSize: number;
   searchTerm: string;
   sortColumn: string;
   sortDirection: SortDirection;
   startIndex: number;
   endIndex: number;
   totalRecords: number;
}

function compare(v1, v2) {
   return v1 < v2 ? -1 : v1 > v2 ? 1 : 0;
}

function sort(contract: any[], column: string, direction: string): any[] {
   if (direction === '') {
      return contract;
   } else {
      return [...contract].sort((a, b) => {
         const res = compare(a[column], b[column]);
         return direction === 'asc' ? res : -res;
      });
   }
}

function matches(contract: any, term: string, pipe: PipeTransform) {
   return contract.CONTRACT_ID.toLowerCase().includes(term)
      || contract.SUBJECT.toLowerCase().includes(term.toLowerCase())
      || contract.SENT_DATE.toLowerCase().includes(term.toLowerCase())
      || contract.Doc_Name.toLowerCase().includes(term.toLowerCase())
      || contract.USER_ID.toLowerCase().includes(term)
}

@Injectable({
   providedIn: 'root'
})

export class ContractService {
   private _loading$ = new BehaviorSubject<boolean>(true);
   private _search$ = new Subject<void>();
   private _contract$ = new BehaviorSubject<any[]>([]);
   private _total$ = new BehaviorSubject<number>(0);
   private _state: State = {
      page: 1,
      pageSize: 10,
      searchTerm: '',
      sortColumn: '',
      sortDirection: '',
      startIndex: 1,
      endIndex: 10,
      totalRecords: 0
   };
   contractData: any;

   constructor(private pipe: DecimalPipe, private _apiService: ApiService) {
      this.getContract();
      this._search$.pipe(
         tap(() => this._loading$.next(true)),
         debounceTime(200),
         switchMap(() => this._search()),
         delay(200),
         tap(() => this._loading$.next(false))
      ).subscribe(result => {
         this._contract$.next(result.contract);
         this._total$.next(result.total);
      });
   }

   async getContract(): Promise<void> {
     this._apiService.post('validate_contract').subscribe(res => {
      this._apiService.get('fetchContract').subscribe(res => {
         this.contractData = res;
         this.contractData.sort((a, b) => {
            const res = compare(a.SENT_DATE, b.SENT_DATE);
            return -res;
         });
         this._search$.next();
      });
     });
   }

   get contract$() { return this._contract$.asObservable(); }
   get total$() { return this._total$.asObservable(); }
   get loading$() { return this._loading$.asObservable(); }
   get page() { return this._state.page; }
   get pageSize() { return this._state.pageSize; }
   get searchTerm() { return this._state.searchTerm; }
   get startIndex() { return this._state.startIndex; }
   get endIndex() { return this._state.endIndex; }
   get totalRecords() { return this._state.totalRecords; }

   set page(page: number) { this._set({ page }); }
   set pageSize(pageSize: number) { this._set({ pageSize }); }
   set startIndex(startIndex: number) { this._set({ startIndex }); }
   set endIndex(endIndex: number) { this._set({ endIndex }); }
   set totalRecords(totalRecords: number) { this._set({ totalRecords }); }
   set searchTerm(searchTerm: string) { this._set({ searchTerm }); }
   set sortColumn(sortColumn: string) { this._set({ sortColumn }); }
   set sortDirection(sortDirection: SortDirection) { this._set({ sortDirection }); }

   private _set(patch: Partial<State>) {
      Object.assign(this._state, patch);
      this._search$.next();
   }

   private _search(): Observable<SearchResult> {
      const { sortColumn, sortDirection, pageSize, page, searchTerm } = this._state;

      let contract = sort(this.contractData, sortColumn, sortDirection);

      contract = contract.filter(table => matches(table, searchTerm, this.pipe));
      const total = contract.length;

      this.totalRecords = contract.length;
      this._state.startIndex = (page - 1) * this.pageSize;
      this._state.endIndex = (page - 1) * this.pageSize + this.pageSize;
      if (this.endIndex > this.totalRecords) {
         this.endIndex = this.totalRecords;
      }
      contract = contract.slice(this._state.startIndex, this._state.endIndex);

      return of(
         { contract, total }
      );
   }

   private getExpireDays(CONTRACT) {
      let expiry_date = new Date(CONTRACT.SENT_DATE);
      expiry_date.setDate(expiry_date.getDate() + CONTRACT.EXPIRY_DAYS);
      const current_date = new Date();
      let Difference_In_Time = expiry_date.getTime() - current_date.getTime();
      let Difference_In_Days = Math.round(Difference_In_Time / (1000 * 3600 * 24));
      return (Difference_In_Days < 1 ? 0 : Difference_In_Days);
   }
}
