import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule, Routes } from '@angular/router';
import { NgbPaginationModule, NgbTypeaheadModule, NgbDropdownModule } from '@ng-bootstrap/ng-bootstrap';
import { NgSelectModule } from '@ng-select/ng-select';
import { SimplebarAngularModule } from 'simplebar-angular';
import { ContractComponent } from './contract.component';
import { ContractCreateComponent } from '../contract-create/contract-create.component';
import { ContractLogComponent } from '../contract-log/contract-log.component';
import { ContractSortableService } from './contract-sortable.directive';

const routes: Routes = [
   { path: '', component: ContractComponent },
   { path: 'create', component: ContractCreateComponent },
   { path: ':id', component: ContractLogComponent },
];

@NgModule({
   declarations: [
      ContractComponent,
      ContractSortableService,
      ContractLogComponent,
      ContractCreateComponent,
   ],
   imports: [
      CommonModule,
      RouterModule.forChild(routes),
      FormsModule,
      ReactiveFormsModule,
      SimplebarAngularModule,
      NgbPaginationModule,
      NgbTypeaheadModule,
      NgSelectModule,
      NgbDropdownModule,
   ]
})
export class ContractModule { }
