import { Component, OnInit, ViewChildren, QueryList } from '@angular/core';
import { DecimalPipe } from '@angular/common';

import { Observable } from 'rxjs';

import { ContractSortableService, SortEvent } from './contract-sortable.directive'
import { ContractService } from './contract.service'
import { Router } from '@angular/router';
import { GetStatus } from '../getStatus';
import Swal from 'sweetalert2';
import { ApiService } from 'src/app/core/services/api.service';

@Component({
   selector: 'app-contract',
   templateUrl: './contract.component.html',
   styleUrls: ['./contract.component.scss'],
   providers: [ContractService, DecimalPipe]

})
export class ContractComponent implements OnInit {
   contract$: Observable<any>;
   total$: Observable<number>;
   submitted = false;
   statusReason: any = {};
   @ViewChildren(ContractSortableService) headers: QueryList<ContractSortableService>;
   file: File;
   fileName: string;
   GetStatus = GetStatus
   constructor(public service: ContractService, private router: Router, private _apiService: ApiService) {
      this.contract$ = service.contract$;
      this.total$ = service.total$;
   }

   ngOnInit(): void {
      this.getResons();
   }

   onSort({ column, direction }: SortEvent) {
      this.headers.forEach(header => {
         if (header.sortable !== column) {
            header.direction = '';
         }
      });
      this.service.sortColumn = column;
      this.service.sortDirection = direction;
   }

   details(CONTRACT) {
      this.router.navigate([`contract/${CONTRACT.CONTRACT_ID}`], {
         state: {
            is_valid_navigation: true,
            contract: CONTRACT
         }
      })
   }

   getExpireDays(CONTRACT) {
      let expiry_date = new Date(CONTRACT.SENT_DATE);
      expiry_date.setDate(expiry_date.getDate() + CONTRACT.EXPIRY_DAYS);
      const current_date = new Date();
      let Difference_In_Time = expiry_date.getTime() - current_date.getTime();
      let Difference_In_Days = Math.round(Difference_In_Time / (1000 * 3600 * 24));
      return (Difference_In_Days < 1 ? 0 : Difference_In_Days);
   }

   getResons() {
      this._apiService.get('Get_Status_Reason').subscribe(res => {

         if (res && res.length > 0) {
            res.forEach(element => {
               this.statusReason[element.REASON_CD] = element.REASONS
            });
         }

         console.log('Inside function' + this.statusReason)
      })

   }

   voidContract(CONTRACT) {
      console.log('Outside function' + this.statusReason)
      Swal.fire({
         position: 'center',
         title: "Are you sure you want to VOID this Contract?",
         showConfirmButton: true,
         icon: 'warning',
         confirmButtonText: 'YES',
         showDenyButton: true,
         input: 'select',
         inputOptions: this.statusReason,
         inputPlaceholder: "Select Reason",
         inputValidator: result => !result && 'Reason required to void this contract!',
      }).then(result => {
         if (result && result.isConfirmed) {
            this._apiService.post(`StatusLog?CONTRACT_ID=${CONTRACT.CONTRACT_ID}&STATUS=${5}&P_REASON_CD=${result.value}`, null).subscribe(res => {
               this.service.getContract();
            })
         }
      });
   }
}
